.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 96px;
  background: rgba(214, 208, 206, 0.80);
  backdrop-filter: blur(8px);
  padding-inline: 32px;
  position: sticky;
  top: 0;
  z-index: 2;
  transition: top 0.6s ease, background-color 0.7s, color 0.7s;

  @media (min-width:320px) and (max-width: 520px) {
    padding-inline: 24px;
  }

  &-dark {
    padding-inline: 32px;
    background: rgba(17, 17, 21, 0.80);
    backdrop-filter: blur(8px);
    transition: top 0.6s ease, background-color 0.7s, color 0.7s;

    @media (min-width:320px) and (max-width: 520px) {
      padding-inline: 24px;
    }
  }

  &.sticky {
    top: -96px;
  }

  &__logoAndNav {
    display: flex;
    align-items: center;
    gap: 56px;
  }

  &__logo {
    cursor: pointer;
  }

  &__profile-and-button {
    display: flex;
    align-items: center;
    gap: 40px;
  }

  &__profile-text {
    position: relative;
    display: flex;
    height: 44px;
    min-width: 44px;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 8px;
    color: var(--Gray-100, #111115);
    font-size: 16px;
    font-weight: 400;
    line-height: 100%;
    cursor: pointer;

    &-dark {
      position: relative;
      display: flex;
      height: 44px;
      min-width: 44px;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      gap: 8px;
      font-size: 16px;
      font-weight: 400;
      line-height: 100%;
      cursor: pointer;
      color: var(--Gray-0, #D5D6DA);

      &:hover {
        text-shadow: 0 0 0.01px #D5D6DA;
      }

      &:active::after {
        border-radius: 8px;
        width: 74%;
      }
    }
  }

  &__button {
    display: flex;
    height: 48px;
    padding: 16px 32px;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    border: 1px solid var(--Gray-80, #444);
    color: var(--Gray-100, #111115);
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: 0.64px;
    text-transform: uppercase;

    &:hover {
      border: 1px solid var(--Gray-60, #575759);
      color: var(--Gray-80, #444);
    }

    &:active {
      padding: 16px 31px;
      outline: none;
      border: 1px solid var(--Gray-60, #111115);
      color: var(--Gray-100, #111115);
      font-weight: 600;
    }

    &-dark {
      display: flex;
      height: 48px;
      padding: 16px 32px;
      justify-content: center;
      align-items: center;
      border: 1px solid var(--Gray-30, #D5D6DA);
      border-radius: 8px;
      color: var(--Gray-0, #F3F4F6);
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      line-height: 100%;
      letter-spacing: 0.64px;
      text-transform: uppercase;

      &:hover {
        border-radius: 8px;
        border: 1px solid var(--Gray-60, #B0B1B5);
      }
  
      &:active {
        padding: 16px 31px;
        outline: none;
        border: 1px solid var(--Gray-0, #F3F4F6);
        color: var(--Gray-0, #F3F4F6);
        font-weight: 600;
      }
    }
  }
}


